import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { 
  HomePage, MainPage, CoursesPage, ExamsPage, NotificationsPage, PersonalDataPage,
  TimetablePage, OpenCoursePage, NewCoursePage
} from '../pages/index.pages';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    MainPage,
    CoursesPage,
    ExamsPage,
    NotificationsPage,
    PersonalDataPage,
    TimetablePage,
    OpenCoursePage,
    NewCoursePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MainPage,
    CoursesPage,
    ExamsPage,
    NotificationsPage,
    PersonalDataPage,
    TimetablePage,
    OpenCoursePage,
    NewCoursePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



